import { useCallback } from 'react';
import { addDoc, collection } from 'firebase/firestore';
import { OWNER_EMAIL, sanitizeShopCategory, sanitizeShopProduct, sanitizeTask, sanitizeUser } from '../data/constants';
import { db } from '../lib/firebase';

export default function useAdminActions({
  currentUser,
  allUsers,
  permissions,
  persistUser,
  showAlert,
  shopCategories,
  shopProducts,
  persistShop
}) {
  const withTargetUser = useCallback(
    async ({ userId, requiredPermission, mutator, successMessage }) => {
      if (!permissions?.[requiredPermission]) {
        showAlert('danger', '      .');
        return false;
      }

      const target = allUsers.find((user) => user.id === userId);
      if (!target) {
        showAlert('warning', '  .');
        return false;
      }

      if (!permissions.isOwner && target.isAdmin) {
        showAlert('danger', ' owner- .');
        return false;
      }

      const draft = JSON.parse(JSON.stringify(target));
      const mutated = mutator(draft) || draft;
      const normalized = sanitizeUser({ ...mutated, id: userId });

      if ((normalized.email || '').toLowerCase() === OWNER_EMAIL) {
        normalized.isAdmin = true;
        normalized.email = OWNER_EMAIL;
      }

      if (!permissions.canManageRoles) {
        normalized.roles = target.roles;
      }

      const saved = await persistUser(userId, normalized, {
        errorMessage: '    .'
      });

      if (saved && successMessage) {
        showAlert('success', successMessage);
      }

      return saved;
    },
    [allUsers, permissions, persistUser, showAlert]
  );

  const onSaveUser = useCallback(
    async (userId, changes) =>
      withTargetUser({
        userId,
        requiredPermission: 'canManageUsers',
        mutator: (draft) => ({
          ...draft,
          ...changes
        }),
        successMessage: '  .'
      }),
    [withTargetUser]
  );

  const onToggleBan = useCallback(
    async (userId, payload = {}) => {
      const target = allUsers.find((user) => user.id === userId);
      if (!target) {
        showAlert('warning', '  .');
        return false;
      }

      const willBan = !target.isBanned;
      const reason = String(payload.reason || '').trim();
      const description = String(payload.description || '').trim();
      const durationDays = Number(payload.durationDays || 0);

      if (willBan && !reason) {
        showAlert('warning', '  .');
        return false;
      }

      let pendingMail = null;

      const saved = await withTargetUser({
        userId,
        requiredPermission: 'canBanDeleteUsers',
        mutator: (draft) => {
          if (draft.isAdmin) {
            return draft;
          }

          if (draft.isBanned) {
            return {
              ...draft,
              isBanned: false,
              banReason: '',
              banDescription: '',
              banExpiresAt: null,
              bannedAt: null
            };
          }

          const now = new Date();
          const banExpiresAt = Number.isFinite(durationDays) && durationDays > 0
            ? new Date(now.getTime() + durationDays * 24 * 60 * 60 * 1000).toISOString()
            : null;

          pendingMail = {
            email: draft.email,
            name: draft.name,
            reason,
            description,
            banExpiresAt
          };

          return {
            ...draft,
            isBanned: true,
            banReason: reason,
            banDescription: description,
            banExpiresAt,
            bannedAt: now.toISOString()
          };
        },
        successMessage: willBan ? ' .' : ' .'
      });

      if (!saved || !willBan || !pendingMail || !db) {
        return saved;
      }

      try {
        const until = pendingMail.banExpiresAt
          ? new Date(pendingMail.banExpiresAt).toLocaleString('uk-UA')
          : '';

        const detailsLine = pendingMail.description
          ? `: ${pendingMail.description}`
          : ':  .';

        await addDoc(collection(db, 'mail'), {
          to: [pendingMail.email],
          message: {
            subject: 'TaskFlow:   ',
            text: `, ${pendingMail.name}.   . : ${pendingMail.reason}. ${detailsLine}  : ${until}.`,
            html: `
              <div style="font-family:Arial,sans-serif;line-height:1.5;">
                <h3> TaskFlow </h3>
                <p>, <strong>${pendingMail.name}</strong>.</p>
                <p><strong>:</strong> ${pendingMail.reason}</p>
                <p><strong>:</strong> ${pendingMail.description || ' '}</p>
                <p><strong> :</strong> ${until}</p>
              </div>
            `
          },
          createdAt: new Date().toISOString()
        });
      } catch {
        showAlert(
          'warning',
          ' ,  email-   ( Firebase Trigger Email).'
        );
      }

      return saved;
    },
    [allUsers, showAlert, withTargetUser]
  );

  const onDeleteUser = useCallback(
    async (userId) =>
      withTargetUser({
        userId,
        requiredPermission: 'canBanDeleteUsers',
        mutator: (draft) => {
          if (draft.isAdmin) {
            return draft;
          }

          return {
            ...draft,
            name: `  (${draft.id.slice(0, 6)})`,
            phone: '',
            avatar: '',
            points: 0,
            subscription: 'free',
            paymentLinked: false,
            tasks: [],
            cart: [],
            purchases: [],
            pointLedger: [],
            settings: {
              reminders: false,
              weeklyDigest: false,
              pushAlerts: false
            },
            roles: [],
            flags: [],
            isBanned: true,
            banReason: '  /',
            banDescription: '   .',
            banExpiresAt: null,
            bannedAt: new Date().toISOString()
          };
        },
        successMessage: ' :     .'
      }),
    [withTargetUser]
  );

  const onSaveRawUser = useCallback(
    async (userId, rawUserJson) => {
      if (!permissions.canUseRawJson) {
        showAlert('danger', ' JSON   .');
        return;
      }

      let parsed;
      try {
        parsed = JSON.parse(rawUserJson);
      } catch {
        showAlert('danger', 'JSON   .');
        return;
      }

      await withTargetUser({
        userId,
        requiredPermission: 'canUseRawJson',
        mutator: () => ({
          ...parsed,
          id: userId
        }),
        successMessage: 'JSON .'
      });
    },
    [permissions.canUseRawJson, showAlert, withTargetUser]
  );

  const onSaveTask = useCallback(
    async (userId, taskInput) => {
      let created = false;

      const saved = await withTargetUser({
        userId,
        requiredPermission: 'canModerateTasks',
        mutator: (draft) => {
          const normalizedTask = sanitizeTask(taskInput);
          const index = draft.tasks.findIndex((task) => Number(task.id) === Number(normalizedTask.id));

          if (index >= 0) {
            draft.tasks[index] = normalizedTask;
          } else {
            draft.tasks.unshift(normalizedTask);
            created = true;
          }

          return draft;
        },
        successMessage: created ? '  .' : '  .'
      });

      return saved;
    },
    [withTargetUser]
  );

  const onDeleteTask = useCallback(
    async (userId, taskId) =>
      withTargetUser({
        userId,
        requiredPermission: 'canModerateTasks',
        mutator: (draft) => ({
          ...draft,
          tasks: draft.tasks.filter((task) => Number(task.id) !== Number(taskId))
        }),
        successMessage: ' .'
      }),
    [withTargetUser]
  );

  const onFlagUser = useCallback(
    async (userId, payload) => {
      const reasonRaw = typeof payload === 'string' ? payload : payload?.reason;
      const cleanReason = String(reasonRaw || '').trim();
      const tags = Array.isArray(payload?.tags) ? payload.tags : [];

      if (!cleanReason) {
        showAlert('warning', '  .');
        return false;
      }

      return withTargetUser({
        userId,
        requiredPermission: 'canFlagAccounts',
        mutator: (draft) => ({
          ...draft,
          flags: [
            ...(Array.isArray(draft.flags) ? draft.flags : []),
            {
              id: `flag-${Date.now()}`,
              reason: cleanReason,
              tags,
              createdAt: new Date().toISOString(),
              createdByUid: currentUser?.id || '',
              createdByName: currentUser?.name || '',
              createdByEmail: currentUser?.email || '',
              createdByRole: permissions.isOwner ? 'owner' : 'staff'
            }
          ]
        }),
        successMessage: ' .'
      });
    },
    [currentUser?.email, currentUser?.id, currentUser?.name, permissions.isOwner, showAlert, withTargetUser]
  );

  const onResolveFlag = useCallback(
    async (userId, flagId) =>
      withTargetUser({
        userId,
        requiredPermission: 'canResolveFlags',
        mutator: (draft) => ({
          ...draft,
          flags: (Array.isArray(draft.flags) ? draft.flags : []).filter((flag) => flag.id !== flagId)
        }),
        successMessage: ' .'
      }),
    [withTargetUser]
  );

  const onAddCategory = useCallback(
    async (inputCategory) => {
      if (!permissions.canManageShop) {
        showAlert('danger', '      .');
        return false;
      }

      const category = sanitizeShopCategory(inputCategory);
      if (!category) {
        showAlert('warning', '  .');
        return false;
      }
      if (shopCategories.includes(category)) {
        showAlert('warning', '   .');
        return false;
      }

      return persistShop([...shopCategories, category], shopProducts, ' .');
    },
    [permissions.canManageShop, persistShop, shopCategories, shopProducts, showAlert]
  );

  const onDeleteCategory = useCallback(
    async (category) => {
      if (!permissions.canManageShop) {
        showAlert('danger', '      .');
        return false;
      }

      if (!shopCategories.includes(category)) {
        return false;
      }

      const nextCategories = shopCategories.filter((item) => item !== category);
      const nextProducts = shopProducts.filter((product) => product.category !== category);

      if (nextCategories.length === 0) {
        showAlert('warning', '     .');
        return false;
      }

      return persistShop(nextCategories, nextProducts, ' .');
    },
    [permissions.canManageShop, persistShop, shopCategories, shopProducts, showAlert]
  );

  const onSaveProduct = useCallback(
    async (inputProduct) => {
      if (!permissions.canManageShop) {
        showAlert('danger', '      .');
        return false;
      }

      const normalized = sanitizeShopProduct(inputProduct);
      if (!normalized.name) {
        showAlert('warning', '  .');
        return false;
      }
      if (!Number.isFinite(normalized.price) || normalized.price <= 0) {
        showAlert('warning', '      0.');
        return false;
      }
      if (!normalized.category) {
        showAlert('warning', '  .');
        return false;
      }

      const hasId = Number.isFinite(Number(inputProduct?.id));
      const nextId = hasId
        ? Number(inputProduct.id)
        : shopProducts.reduce((max, product) => Math.max(max, Number(product.id) || 0), 0) + 1;

      const nextProduct = {
        ...normalized,
        id: nextId
      };

      const nextProducts = hasId
        ? shopProducts.map((product) => (product.id === nextId ? nextProduct : product))
        : [...shopProducts, nextProduct];

      const nextCategories = shopCategories.includes(nextProduct.category)
        ? shopCategories
        : [...shopCategories, nextProduct.category];

      return persistShop(nextCategories, nextProducts, hasId ? ' .' : ' .');
    },
    [permissions.canManageShop, persistShop, shopCategories, shopProducts, showAlert]
  );

  const onDeleteProduct = useCallback(
    async (productId) => {
      if (!permissions.canManageShop) {
        showAlert('danger', '      .');
        return false;
      }

      const nextProducts = shopProducts.filter((product) => product.id !== productId);
      if (nextProducts.length === shopProducts.length) {
        return false;
      }

      return persistShop(shopCategories, nextProducts, ' .');
    },
    [permissions.canManageShop, persistShop, shopCategories, shopProducts, showAlert]
  );

  return {
    onSaveUser,
    onToggleBan,
    onDeleteUser,
    onSaveRawUser,
    onSaveTask,
    onDeleteTask,
    onFlagUser,
    onResolveFlag,
    onAddCategory,
    onDeleteCategory,
    onSaveProduct,
    onDeleteProduct
  };
}
