import { useMemo, useState } from 'react';
import { getProductFinalPrice } from '../../data/constants';

const PRODUCTS_PER_PAGE = 9;

export default function ShopPage({ products, categories, onAddToCart }) {
  const [query, setQuery] = useState('');
  const [activeCategory, setActiveCategory] = useState('all');
  const [newOnly, setNewOnly] = useState(false);
  const [page, setPage] = useState(1);

  const filteredProducts = useMemo(() => {
    const search = query.trim().toLowerCase();

    return products.filter((product) => {
      const byCategory = activeCategory === 'all' ? true : product.category === activeCategory;
      if (!byCategory) {
        return false;
      }

      if (newOnly && !product.isNew) {
        return false;
      }

      if (!search) {
        return true;
      }

      const haystack = `${product.name} ${product.category}`.toLowerCase();
      return haystack.includes(search);
    });
  }, [products, query, activeCategory, newOnly]);

  const pagination = useMemo(() => {
    const totalPages = Math.max(1, Math.ceil(filteredProducts.length / PRODUCTS_PER_PAGE));
    const safePage = Math.min(page, totalPages);
    const start = (safePage - 1) * PRODUCTS_PER_PAGE;

    return {
      page: safePage,
      totalPages,
      items: filteredProducts.slice(start, start + PRODUCTS_PER_PAGE)
    };
  }, [filteredProducts, page]);

  return (
    <div className="d-flex flex-column gap-4">
      <div className="card shadow-sm">
        <div className="card-body">
          <div className="row g-3 align-items-end">
            <div className="col-md-5">
              <label className="form-label"> </label>
              <input
                className="form-control"
                placeholder=": , , "
                value={query}
                onChange={(event) => {
                  setQuery(event.target.value);
                  setPage(1);
                }}
              />
            </div>
            <div className="col-md-4">
              <label className="form-label"></label>
              <select
                className="form-select"
                value={activeCategory}
                onChange={(event) => {
                  setActiveCategory(event.target.value);
                  setPage(1);
                }}
              >
                <option value="all"> </option>
                {categories.map((category) => (
                  <option key={category} value={category}>
                    {category}
                  </option>
                ))}
              </select>
            </div>
            <div className="col-md-3">
              <label className="form-label d-block">-</label>
              <button
                type="button"
                className={`btn w-100 ${newOnly ? 'btn-primary' : 'btn-outline-primary'}`}
                onClick={() => {
                  setNewOnly((value) => !value);
                  setPage(1);
                }}
              >
                <i className="bi bi-stars me-2" />
                {newOnly ? ' ' : ' '}
              </button>
            </div>
          </div>
        </div>
      </div>

      <div className="row g-4">
        {pagination.items.map((product) => {
          const finalPrice = getProductFinalPrice(product);
          const hasDiscount = finalPrice < product.price;

          return (
            <div className="col-sm-6 col-xl-4" key={product.id}>
              <div className="card h-100 shadow-sm shop-card">
                <div className="card-body d-flex flex-column">
                  <div className="product-image-wrap mb-3 position-relative">
                    <img
                      src={product.image}
                      alt={product.name}
                      className="product-image"
                      loading="lazy"
                      onError={(event) => {
                        event.currentTarget.src = 'https://placehold.co/600x400?text=TaskFlow+Shop';
                      }}
                    />
                    <div className="product-badges">
                      {product.isNew && <span className="badge product-badge-new"></span>}
                      {hasDiscount && (
                        <span className="badge product-badge-discount">-{product.discountPercent}%</span>
                      )}
                    </div>
                  </div>

                  <div className="d-flex align-items-center gap-2 mb-2">
                    <span className="badge text-bg-light">{product.category}</span>
                  </div>

                  <h5 className="card-title">{product.name}</h5>

                  <div className="d-flex justify-content-between align-items-end mt-auto pt-3">
                    <div>
                      <div className="product-price">
                        <i className="bi bi-coin me-1" />
                        {finalPrice}
                      </div>
                      {hasDiscount && (
                        <div className="product-price-old">
                          <i className="bi bi-coin me-1" />
                          {product.price}
                        </div>
                      )}
                    </div>

                    <button
                      className="btn btn-outline-primary btn-sm"
                      type="button"
                      onClick={() => onAddToCart(product.id)}
                    >
                       
                    </button>
                  </div>
                </div>
              </div>
            </div>
          );
        })}

        {pagination.items.length === 0 && (
          <div className="col-12">
            <div className="card shadow-sm">
              <div className="card-body text-body-secondary">     .</div>
            </div>
          </div>
        )}
      </div>

      {pagination.totalPages > 1 && (
        <div className="d-flex justify-content-center">
          <div className="btn-group" role="group" aria-label="Shop pagination">
            <button
              type="button"
              className="btn btn-outline-primary"
              disabled={pagination.page <= 1}
              onClick={() => setPage((prev) => Math.max(1, prev - 1))}
            >
              
            </button>
            <button type="button" className="btn btn-primary" disabled>
              {pagination.page} / {pagination.totalPages}
            </button>
            <button
              type="button"
              className="btn btn-outline-primary"
              disabled={pagination.page >= pagination.totalPages}
              onClick={() => setPage((prev) => Math.min(pagination.totalPages, prev + 1))}
            >
              
            </button>
          </div>
        </div>
      )}
    </div>
  );
}
