import { getProductFinalPrice } from '../../../data/constants';

export default function AdminShopSection({
  shopCategories,
  shopProducts,
  categoryInput,
  onCategoryInputChange,
  onAddCategory,
  onDeleteCategory,
  editingProductId,
  productForm,
  onProductFormChange,
  onProductSubmit,
  onResetProductEdit,
  productSearch,
  onProductSearchChange,
  productPagination,
  onProductPageChange,
  onEditProduct,
  onDeleteProduct
}) {
  return (
    <div className="row g-4">
      <div className="col-lg-4">
        <div className="card shadow-sm h-100">
          <div className="card-body">
            <h6 className="mb-3"> </h6>

            <div className="input-group mb-3">
              <input
                className="form-control"
                placeholder=" "
                value={categoryInput}
                onChange={(event) => onCategoryInputChange(event.target.value)}
              />
              <button className="btn btn-primary" type="button" onClick={onAddCategory}>
                
              </button>
            </div>

            <div className="admin-list-scroll d-flex flex-column gap-2">
              {shopCategories.map((category) => (
                <div className="admin-category-item" key={category}>
                  <div>
                    <strong>{category}</strong>
                    <div className="small text-body-secondary">
                      : {shopProducts.filter((item) => item.category === category).length}
                    </div>
                  </div>
                  <button
                    type="button"
                    className="btn btn-sm btn-outline-danger"
                    onClick={() => onDeleteCategory(category)}
                  >
                    <i className="bi bi-trash" />
                  </button>
                </div>
              ))}
            </div>
          </div>
        </div>
      </div>

      <div className="col-lg-8">
        <div className="card shadow-sm mb-4">
          <div className="card-body">
            <div className="d-flex justify-content-between align-items-center mb-3">
              <h6 className="mb-0">{editingProductId ? ' ' : ' '}</h6>
              {editingProductId && (
                <button type="button" className="btn btn-sm btn-outline-secondary" onClick={onResetProductEdit}>
                  
                </button>
              )}
            </div>

            <form className="row g-3" onSubmit={onProductSubmit}>
              <div className="col-md-6">
                <label className="form-label"></label>
                <input
                  className="form-control"
                  value={productForm.name}
                  onChange={(event) => onProductFormChange('name', event.target.value)}
                />
              </div>
              <div className="col-md-3">
                <label className="form-label"> ()</label>
                <input
                  type="number"
                  className="form-control"
                  value={productForm.price}
                  onChange={(event) => onProductFormChange('price', event.target.value)}
                />
              </div>
              <div className="col-md-3">
                <label className="form-label"> (%)</label>
                <input
                  type="number"
                  className="form-control"
                  value={productForm.discountPercent}
                  onChange={(event) => onProductFormChange('discountPercent', event.target.value)}
                />
              </div>
              <div className="col-md-8">
                <label className="form-label"></label>
                <input
                  list="shop-categories"
                  className="form-control"
                  value={productForm.category}
                  onChange={(event) => onProductFormChange('category', event.target.value)}
                />
                <datalist id="shop-categories">
                  {shopCategories.map((category) => (
                    <option key={category} value={category} />
                  ))}
                </datalist>
              </div>
              <div className="col-md-4 d-flex align-items-end">
                <label className="form-check form-switch mb-2">
                  <input
                    className="form-check-input"
                    type="checkbox"
                    checked={productForm.isNew}
                    onChange={(event) => onProductFormChange('isNew', event.target.checked)}
                  />
                  <span className="form-check-label">  </span>
                </label>
              </div>
              <div className="col-12">
                <label className="form-label"> (URL)</label>
                <input
                  className="form-control"
                  value={productForm.image}
                  placeholder="https://..."
                  onChange={(event) => onProductFormChange('image', event.target.value)}
                />
              </div>
              <div className="col-12 d-flex justify-content-end">
                <button type="submit" className="btn btn-primary">
                  {editingProductId ? ' ' : ' '}
                </button>
              </div>
            </form>
          </div>
        </div>

        <div className="card shadow-sm">
          <div className="card-body">
            <div className="d-flex justify-content-between align-items-center mb-3 gap-2">
              <h6 className="mb-0"> </h6>
              <input
                className="form-control admin-product-search"
                placeholder=" "
                value={productSearch}
                onChange={(event) => onProductSearchChange(event.target.value)}
              />
            </div>

            <div className="row g-3">
              {productPagination.items.map((product) => {
                const finalPrice = getProductFinalPrice(product);
                const hasDiscount = finalPrice < product.price;

                return (
                  <div className="col-md-6" key={product.id}>
                    <div className="admin-product-card">
                      <img src={product.image} alt={product.name} className="admin-product-image" />
                      <div className="d-flex flex-wrap gap-1">
                        {product.isNew && <span className="badge product-badge-new"></span>}
                        {hasDiscount && <span className="badge product-badge-discount">-{product.discountPercent}%</span>}
                      </div>
                      <div className="d-flex justify-content-between align-items-start gap-2">
                        <div>
                          <div className="fw-semibold">{product.name}</div>
                          <div className="small text-body-secondary">{product.category}</div>
                          <div className="small text-primary fw-semibold mt-1">{finalPrice} </div>
                          {hasDiscount && (
                            <div className="small text-decoration-line-through text-body-secondary">{product.price} </div>
                          )}
                        </div>
                        <div className="d-flex gap-2">
                          <button type="button" className="btn btn-sm btn-outline-primary" onClick={() => onEditProduct(product)}>
                            <i className="bi bi-pencil" />
                          </button>
                          <button type="button" className="btn btn-sm btn-outline-danger" onClick={() => onDeleteProduct(product.id)}>
                            <i className="bi bi-trash" />
                          </button>
                        </div>
                      </div>
                    </div>
                  </div>
                );
              })}

              {productPagination.items.length === 0 && (
                <p className="text-body-secondary mb-0">  .</p>
              )}
            </div>

            {productPagination.totalPages > 1 && (
              <div className="d-flex justify-content-center mt-3">
                <div className="btn-group" role="group" aria-label="Products pagination">
                  <button
                    type="button"
                    className="btn btn-outline-primary btn-sm"
                    disabled={productPagination.page <= 1}
                    onClick={() => onProductPageChange(Math.max(1, productPagination.page - 1))}
                  >
                    
                  </button>
                  <button type="button" className="btn btn-primary btn-sm" disabled>
                    {productPagination.page} / {productPagination.totalPages}
                  </button>
                  <button
                    type="button"
                    className="btn btn-outline-primary btn-sm"
                    disabled={productPagination.page >= productPagination.totalPages}
                    onClick={() => onProductPageChange(Math.min(productPagination.totalPages, productPagination.page + 1))}
                  >
                    
                  </button>
                </div>
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  );
}
